.register {

    max-width: 400px;
    width: 100vw;
    height: 100vh;
    max-height: 850px;

    .heading {
        font-size: 50px;
        font-weight: 500;
        padding-top: 10px;
        text-align: center;
        font-family: 'Redressed';
        margin-bottom: 20px;
        position: relative;

        span {
            font-size: 10px;
            position: absolute;
            bottom: 4px;
            right: 130px;
            font-family: Arial, Helvetica, sans-serif;
        }
    }

    form {
        width: 320px;
        margin: auto;
        font-size: 13px;

        input{
            font-size: 12px;
        }

        .alert{
            font-size: 12px;
            color: red;
            position: absolute;
            margin-top: -15px;
            margin-left: -15px;
            font-style: italic;
        }

        p {
            margin-top: 15px;
            margin-bottom: 5px;
            font-weight: 600;
        }

        span {
            color: red;
        }

        #s1,
        #s2 {
            width: 150px;
            text-align: center;
            height: 35px;
            border-radius: 4px;
            margin-top: 15px;
            border-color: rgb(199, 199, 199);
            color: gray;
        }

        #s2 {
            margin-left: 20px;
        }

        .btn {
            width: 100%;
            background-color: #0F55A7;
            margin-top: 20px;
            color: white;
            margin-bottom: 10px;
            font-size: 14px;
        }


        .uploadSection {
            width: 180px;
            height: 80px;
            margin: auto;
            margin-top: 10px;
            border-radius: 10px;
            border: 1px solid rgb(199, 199, 199);
            position: relative;

            .upBtn {
                width: 45px;
                height: 35px;
                border-radius: 4px;
                margin: auto;
                margin-top: 15px;
                color: white;
                display: grid;
                place-content: center;
            }
            #colBt1{
                background-color: #0F55A7;
            }
            #colBt2{
                background-color: rgb(40, 200, 40);
            }

            #file {
                width: 45px;
                position: absolute;
                top: 15px;
                left: 66.5px;
                height: 35px;
                opacity: 0;
            }

            p {
                text-align: center;
                margin-top: 10px;
                color: #797979;
                font-size: 10px;
            }
        }
    }
}
