.qrVerify {
    width: 100vw;
    max-width: 400px;
    max-height: 850px;
    height: 100vh;
    position: relative;
    background-color: white;


    .warrantyLoader {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0px;
        left: 0px;
        background-color: rgb(255, 255, 255, 0.95);
        z-index: 20;
        display: grid;
        place-content: center;
    }

    .qrInnerVerify {
        width: 100%;
        height: 100vh;
        max-height: 850px;
        position: relative;
        overflow-y: scroll;

        &::-webkit-scrollbar {
            display: none;
        }


        .companyLogo {
            width: 140px;
            height: 50px;
            margin: auto;
            border-radius: 5px;
            margin-top: 25px;

            img {
                width: 100%;
                height: 100%;
                border-radius: 5px;
            }
        }

        .name {
            width: 85%;
            margin: auto;
            margin-top: 25px;
            font-size: 18px;
            color: #474747;
            font-family: "Roboto";
            font-weight: bolder;
            letter-spacing: .5px;
            text-transform: capitalize;
        }


        .indicatorBox {
            width: 85%;
            height: 214px;
            margin: auto;
            margin-top: 15px;
            border-radius: 24px;
            padding: 20px 10px 0px 10px;
            color: white;
            background-color: #D9D9D9;

            .gifBox {
                width: 80px;
                height: 80px;
                background-color: white;
                border-radius: 50%;
                margin: auto;

                img {
                    width: 100%;
                    height: 100%;
                }
            }

            .p1 {
                text-align: center;
                margin-top: 5px;
            }

            .p2 {
                text-align: center;
                padding-top: 5px;
                font-size: 12px;
            }

            section {
                font-size: 10px;
                text-align: center;
                margin-top: -8px;
                // background-color: blue;

                div {
                    display: inline-block;
                    width: 10.36px;
                    height: 10.86px;
                    position: relative;
                    top: 1.7px;
                    background: #0F55A7;
                    border-radius: 1px;

                    img {
                        width: 90%;
                        height: 90%;
                        position: relative;
                        top: -3.5px;
                        left: -0.8px;
                    }
                }
            }
        }


        .redColor {
            background-color: #F7323F;
        }

        .greenColor {
            background-color: #27AD5F;
        }



        #text {
            width: 85%;
            margin: auto;
            text-align: center;
            font-size: 14px;
            padding-top: 20px;
            font-weight: 500;
            color: #474747;

            span {
                color: #0B69D8;
                cursor: pointer;
            }
        }

        .productSection {
            width: 85%;
            margin: auto;
            padding-top: 20px;
            padding-bottom: 20px;

            .productImages {
                margin: auto;
                width: 100%;
                height: auto;
                // background-color: #D9D9D9;
                margin-top: 20px;
                margin-bottom: 20px;

                img {
                    width: 100%;
                    height: 100%;
                }
            }

            .productName {
                font-size: 25px;
                font-weight: 600;
                color: #474747;
                text-transform: capitalize;
            }

            .productDetail {
                width: 100%;
                font-size: 14px;
                color: #797979;
            }

            .menufacture{
                color: #797979;
                margin-top: 10px;
                font-size: 13px;
                strong{
                    font-weight: 600;
                }
            }

            .additional {
                font-size: 14px;
                color: #797979;
                margin-top: 20px;
                padding-top: 11px;
                margin-bottom: 20px;
                padding-bottom: 20px;
                border-bottom: 1px solid #E8E8E8;
                border-top: 1px solid #E8E8E8;

                div {
                    margin-top: 8px;

                    strong {
                        font-weight: 600;
                    }
                }
            }

            .des {
                section {
                    color: #474747;
                    font-weight: 600;
                }

                p {
                    margin-top: 5px;
                    font-size: 14px;
                    color: #797979;
                }
            }
        }

        .activateBtn {
            width: 100%;
            position: fixed;
            bottom: 20px;
            left: 0px;
            text-align: center;
            z-index: 10;

            .bt {
                width: 80%;
                max-width: 330px;
                height: 45px;
                text-transform: none;
                background: #0B69D8;
                color: white;
                font-weight: bold;
            }
        }

        .video {
            width: 85%;
            height: 180px;
            background: #D9D9D9;
            margin: auto;
            margin-bottom: 20px;

            iframe {
                width: 100%;
                height: 100%;
            }
        }

        .footer {
            width: 100%;
            height: 30px;
            padding-bottom: 100px;

            .grid {
                width: 150px;
                margin: auto;
                display: grid;
                grid-template-columns: 1fr 1fr;

                div {
                    display: grid;
                    place-content: center;
                    font-size: 11px;
                    color: #0F55A7;

                    img {
                        position: relative;
                        width: 80%;
                        height: auto;
                        top: 2px;
                    }
                }
            }
        }

    }
}