.productCard {
    width: 200px;
    height: 265px;
    background-color: white;
    border-radius: 20px;
    margin: 10px;
    display: inline-block;
    box-shadow: 1px 1px 10px rgb(225, 230, 241);
    padding: 15px;
    padding-bottom: 25px;
    position: relative;
    cursor: pointer;

    .imageDiv {
        width: 170px;
        height: 160px;

        img {
            width: 100%;
            height: 100%;
            border-radius: 20px;
        }
    }


    section {
        font-size: 18px;
        font-weight: 600;
        margin-top: 15px;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
    }

    .brandName {
        margin-top: 5px;
        font-size: 14px;
        color: gray;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        width: 80%;
    }

    .call {
        position: absolute;
        right: 15px;
        bottom: 15px;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        background-color: white;
        box-shadow: 1px 1px 10px rgb(223, 229, 234);
        text-align: center;
        color: #0F55A7;
    }
}