.registerForm {
    width: 100vw;
    height: 100vh;
    max-width: 400px;
    background: #0F55A7;
    position: relative;
    max-height: 850px;
    overflow: hidden;


    .warrantyLoader {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.4);
        z-index: 50;
        display: grid;
        place-content: center;
    }


    #inner {
        width: 100%;
        height: 93vh;
        background: white;
        position: absolute;
        bottom: -200px;
        border-radius: 30px 30px 0px 0px;
        transition: ease .5s all;


        .innerScroll {
            margin-top: 15px;
            overflow-y: scroll;

            &::-webkit-scrollbar {
                display: none;
            }

            height: 87vh;
        }

        #line {
            width: 50px;
            height: 5px;
            background-color: #C6C6C6;
            margin: auto;
            margin-top: 15px;
            border-radius: 3px;
        }

        .but1 {
            position: absolute;
            top: 10px;
            right: 15px;
            color: #D9D9D9;
            border: none;
            padding: 2px;
        }

        .head {
            text-align: center;
            width: 70%;
            margin: auto;
            margin-top: 20px;

            div {
                color: #474747;
                font-weight: 700;
                font-size: 17px;
                letter-spacing: .25px;
            }

            section {
                font-weight: 500;
                margin-top: 8px;
                color: #797979;
                font-size: 12px;
            }
        }

        form {
            width: 80%;
            margin: auto;
            margin-top: 20px;

            .form-group {
                margin-top: 15px;
                position: relative;

                span{
                    font-size: 12px;
                    color: red;
                }

                label {
                    font-weight: 600;
                    color: #474747;
                    margin-bottom: 5px;
                    font-size: 14px;
                    span{
                        font-size: 16px;
                    }
                }

                input {
                    border-color: #C6C6C6;
                    line-height: 25px;
                    border-radius: 8px;
                    font-size: 12px;
                    font-weight: 500;
                    text-transform: capitalize;

                    &::placeholder {
                        color: #C6C6C6;
                    }
                }

                .selectProduct {
                    position: absolute;
                    width: 100%;
                    height: 200px;
                    background-color: white;
                    box-shadow: 0px 0px 10px #C6C6C6;
                    z-index: 2;
                    border-radius: 10px;
                    margin-top: 10px;
                    padding: 10px;
                    padding-top: 0px;
                    padding-bottom: 15px;
                    overflow-y: scroll;

                    &::-webkit-scrollbar {
                        display: none;
                    }

                }

                .boxVal {
                    border-radius: 8px;
                    border-color: #C6C6C6;
                    height: 35px;
                    cursor: pointer;
                    font-size: 12px;
                    font-weight: 500;
                    display: flex;
                    align-self: center;
                    text-transform: capitalize;
                }

                .selectBox {
                    width: 100%;
                    padding: 10px;
                    padding-top: 5px;
                    background: white;
                    box-shadow: 0px 0px 10px #ebebeb;
                    border-radius: 10px;
                    margin-top: 5px;
                    position: absolute;
                    z-index: 10;

                    .but {
                        border-radius: 10px;
                        width: 100%;
                        margin-top: 5px;
                        font-size: 11px;
                        text-transform: none;
                        border: 1px solid #CFE5FF;
                        font-weight: bold;
                        height: 40px;
                    }
                }

                .arrowDown {
                    position: absolute;
                    bottom: 0px;
                    right: 5px;
                    color: #797979;
                }
            }

            .invoiceBox {
                width: 100%;
                border-radius: 8px;
                margin: auto;
                height: 75px;
                border: 1px dashed #C6C6C6;
                border-radius: 10px;
                text-align: center;
                position: relative;

                input {
                    position: absolute;
                    top: 0px;
                    left: 0px;
                    opacity: 0;
                    width: 100%;
                    height: 100%;
                    cursor: pointer;
                }

                div {
                    padding: 3px;
                    color: #C6C6C6;
                }

                .section1,
                .section2 {
                    font-size: 13px;
                    color: #C6C6C6;
                    margin-bottom: -8px;
                }

                .section2 {
                    color: black;
                }

                span {
                    font-size: 9px;
                    color: #6B8BAF;
                }
            }

        }

        .genBox {
            text-align: center;
            margin-top: 35px;
            width: 100%;

            .generate {
                width: 80%;
                border-radius: 10px;
                text-transform: none;
                height: 45px;
                background-color: #0B69D8;
                font-weight: bold;
            }
        }

    }

}